.CardOne {
	position: absolute;
	width: 366px;
	height: 545px;
	left: 37px;
	top: 130px;
	color: white;
	text-align: center;
	background: #363738;
	.CardOne__title {
		margin-top: 50px;
	}
	.iconP {
		width: 250px;
		height: 250px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 auto;
		margin-top: 50px;
		background: #ffffff;
		border-radius: 8px;
	}
	.Add {
		position: absolute;
		width: 366px;
		height: 58px;
		text-align: center;
		display: flex;
		justify-content: center;
		align-items: center;
		top: 545px;
		font-weight: bold;
		font-size: 36px;
		background: #000000;
		p {
			&:hover {
				cursor: pointer;
			}
		}
	}
}
