.header {
	width: 1600px;
	height: 50px;
	background-color: #c4c4c4;
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	.Tlg {
		margin: 0 auto;
		font-weight: 600;
		&:hover {
			cursor: pointer;
		}
	}
	.logoIcon {
		width: 70px;
		position: absolute;
		left: 20px;
	}
	.telefon {
		letter-spacing: 0.1em;
		font-weight: 700;
		position: absolute;
		right: 20px;
		&:hover {
			cursor: pointer;
		}
	}
}
